<template lang="pug">
  .push-block(:class="{removed_block: rem}")
    .push__img
      img(:src="info.photo" :alt="info.entity_author.first_name")
    p.push__content
      router-link.push__content-name(:to="getRouteByNotification(info)")
        | {{info.entity_author.first_name + ' ' + info.entity_author.last_name  + ' '}}
        |
        | {{' ' + getNotificationsTextType(info.event_type) + ' '}}
      span.push__content-preview «{{info.info}}»
    span.push__time {{info.sent_time | moment('from')}}
    .push-block__actions.delete(v-tooltip.bottom="'Удалить'" @click="onDeleteItem(info.id)")
          simple-svg(:filepath="'/static/img/delete.svg'")
</template>

<script>
import {mapActions, mapGetters} from 'vuex'
import { getRouteByNotification } from '@/utils/notifications.utils.js'
export default {
  name: 'PushBlock',
  data: () => ({
    rem: false
  }),
  props: {
    info: Object
  },
  computed: {
    ...mapGetters('profile/notifications', ['getNotificationsTextType', 'getNotificationsLength'])
  },
  methods: {
    ...mapActions('profile/notifications', ['readOneNotification', 'apiNotifications']),
    getRouteByNotification,
    onDeleteItem(id) {
      this.rem = true
      setTimeout(()=> {this.readOneNotification(id).then(() => {if (this.getNotificationsLength === 0) this.$router.push('News')})},500)
    }
  }
}
</script>

<style lang="stylus">
.push-block {
  background: #fff;
  padding: 25px 30px;
  box-shadow: 0px 2px 60px rgba(0, 0, 0, 0.1);
  display: flex;

  &+& {
    margin-top: 20px;
  }

  &.removed_block {
    background: #ffe5e5;
    transform: translate(1500px,0);
    transition: all 1s ease;
  }

  .push__img {
    flex: none;
  }

  .push__content {
    max-width: 650px;
    padding-top: 10px;
  }

  .push__time {
    flex: none;
    padding-top: 20px;
  }

  .push-block__actions {
    cursor: pointer;

    @media (max-width: breakpoint-xxl) {
      &+& {
        margin-left: 5px;
      }
    }

    &+& {
      margin-left: 10px;
    }

    &.delete {
      margin-top: 20px;
      margin-left: 25px;
    }

    .simple-svg-wrapper {
      width: 20px;
      height: 20px;
    }
  }

}
</style>



